.Button {
  margin-bottom: 16px;
}

.StatusBox {
  display: grid;
  gap: 10px;
  grid-template-columns: auto auto 1fr;
}

.StatusBox_Graph {
  display: grid;
  gap: 2px;
  grid-template-columns: repeat(auto-fill, 10px);
}

.StatusBox_GraphRect {
  width: 10px;
  height: 10px;
}

.StatusBox_Graph.-Processing .StatusBox_GraphRect {
  background-color: #268bd2;
}

.StatusBox_Graph.-Done .StatusBox_GraphRect {
  background-color: #859900;
}
